.container {
	display: flex;
	align-items: center;
	flex-direction: column;
}

.logo {
	max-height: 260px;
}

.text {
	margin-left: 20px;
	opacity: 0.8;
}

.head {
	font-weight: 500;
	font-size: 24px;
	margin: 0;
	margin-bottom: 10px;
}

.body {
	font-size: 14px;
	margin: 0;
}

.paymentImage {
	/* width: 40px; */
	height: 40px;
	margin-right: 40px;
	margin-bottom: 20px;
}
.paymentImage:last-child {
	margin-right: 0;
}

.paymentContainer {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 50px;
}

.paymentContainerImages {
	/* display: flex; */
	text-align: center;
}

.textContainer {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.title {
	font-size: 30px;
}
.spanTitle {
	font-size: 90px;
}

@media screen and (max-width: 800px) {
	.textContainer {
		flex-direction: column;
	}
	.logo {
		max-height: none;
		width: 70%;
		margin-bottom: 20px;
	}
	.title {
		font-size: 30px;
	}
	.spanTitle {
		font-size: 70px;
	}
}

@media screen and (max-width: 440px) {
	.spanTitle {
		font-size: 50px;
	}
}
